@import 'mixins.scss';
@import 'vars.scss';

.header {
    
    &__top {
        padding-top: 35px;
        padding-bottom: 20px;
        background-color: $grey-color;

        .container {
            display: flex;
            justify-content: space-between;
        }

        &-container {
            display: flex;
            align-items: center;
            column-gap: 25px;
        }
    }
    &__logo {
        height: 80px;

        img {
            object-fit: contain;
        }
    }
    &__collection {
        width: 35px;
        height: 35px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        cursor: pointer;
        transition: $transition;
        background-image: url("../img/icons/LOGO-black.png");

        &:hover {
            background-image: url("../img/icons/LOGO-pink.png");
        }
    }
    &__login {
        width: 35px;
        height: 35px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        background-image:url("../img/icons/personal-kabinet.png");
        transition: $transition;

        &:hover {
            background-image:url("../img/icons/personal-kabinet-pink.png");
        }
    }
    &__search{
        display: flex;
        overflow: hidden;
        align-items: center;
        cursor: pointer;

        input[type="submit"]{
            display: none;
        }
        &-input {
            transition: $transition;
            position: relative;
            right: -120%;
            max-width: 300px;

            &.active {
                right: -15px;
            }
            & input[type="text"]{
                width: 100%;
                height: 25px;
                border: none;
                padding: 5px;
                padding-left: 10px;
                outline: none;
                box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
            }
        }
        &-icon {
            width: 35px;
            height: 35px;
            background-repeat: no-repeat;
            background-size: contain;
            position: relative;
            z-index: 2;
            background-image: url("../img/icons/search-icon.png");
            background-position: center;
            transition: $transition;

            &:hover {
                background-image: url("../img/icons/search-icon-hover.png");
            }
        }
    }
    &__bottom {
        width: 100%;
        background-color: $black-color;
    }
    &__mobile-menu {
        display: none;
    }
    &__burger-btn {
        display: none;
    }
    &__menu {
        margin: 0;
        display: flex;
        width: 100%;

        &-link {
            margin: 0;
            display: flex;
            padding-top: 25px;
            padding-bottom: 25px;
            padding-left: 0;
            justify-content: center;
            align-items: center;
            width: 229px;
            border-left: 1px solid #fff;
            cursor: pointer;
            transition: $transition;

            &:hover {
                border-color: #ff51ce;

                & + .header__menu-link {
                    border-color: #ff51ce;
                }
            }

            a {
                @include font-raleway;
                color: #f8f8f8;
                position: relative;
                font-size: 18px;
                line-height: 110%;
                text-align: center;
                font-weight: 700;

                &::before {
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: -6px;
                    width: 100%;
                    height: 2px;
                    background-color: #fff;
                    transform: scaleX(0);
                    transition: $transition;
                }
                &:hover::before {
                    transform: scaleX(1);
                }
            }

            &:first-child {
                border-left: 0;
            }
        }
    }
}

@media(max-width:1160px){
    .header__top {
        padding-left: 15px;
        padding-right: 15px;
    }
}