.about-shiny {
    background-color: #242424;

    .container {
        padding: 65px 60px;
    }

    &__title {
        font-weight: 700;
        font-size: 48px;
        line-height: 120%;
        letter-spacing: -0.05em;
        text-transform: uppercase;
        color: #fff;
        margin: 0;
        margin-bottom: 45px;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
    }
    &__text {
        @include main-page-text;
        color: rgba(255, 255, 255, 0.83);
        max-width: 650px;
    }
    &__image {
        min-width: 325px;
    }
}