@mixin main-page-title {
    font-size: 64px;
    font-weight: 700;
    line-height: 120%;
    letter-spacing: -0.05em;
    text-transform: uppercase;
}
@mixin page-title-two {
    font-size: 48px;
    font-weight: 700;
    letter-spacing: -0.03em;
    line-height: 120%;
}
@mixin main-page-text {
    font-size: 24px;
    line-height: 110%;
    font-weight: 500;
}
@mixin article-text {
    font-size: 18px;
    line-height: 120%;
    font-weight: 400;
}
@mixin font-alatsi {
    font-family: 'Alatsi';
}
@mixin font-raleway {
    font-family: 'Raleway';
}
@mixin font-advent-pro {
    font-family: 'AdventPro';
}