@import 'mixins.scss';
@import 'vars.scss';

.footer {
    background-color: $black-color;
    padding: 25px 0;

    &__container {
        display: flex;
        justify-content: space-between;
        padding-left: 50px;
    }
    &__menu {
        display: flex;
        column-gap: 55px;
        color: #dbdbdb;

        &-link {
            padding: 0;
            text-transform: uppercase;
            font-size: 24px;
            font-weight: 400;
            line-height: 120%;
            letter-spacing: -0.04em;
            margin-bottom: 18px;

            a {
                color: #fff;
                transition: $transition;
                position: relative;

                &::before {
                    content: "";
                    width: 100%;
                    height: 2px;
                    background-color: #fff;
                    position: absolute;
                    left: 0;
                    bottom: -6px;
                    transform: scaleX(0);
                    transition: $transition;
                }
                &:hover::before {
                    transform: scaleX(1);
                }
            }
        }
        &-right {
            margin: 0;
            margin-top: 50px;
        }
        &-left {
            margin: 0;
            margin-top: 50px;
        }
    }
    &__social {
        display: flex;
        column-gap: 30px;

        &-link {
            width: 50px;
            height: 50px;
        }
    }
    &__right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__copyright {
        padding-top: 15px;
        padding-bottom: 15px;
        text-align: center;

        &-link {
            color: #fff;
            font-size: 24px;
            border-bottom: 1px solid #fff;
        }
    }
}