@import 'mixins.scss';
@import 'vars.scss';

* {
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    @include font-raleway;
    box-sizing: border-box;
    background-color: #fff;
}

.container {
    width: 1148px;
    margin: 0 auto;
}

p {
    margin: 0;
    margin-bottom: 15px;
}
  
img {
    display: block;
    max-width: 100%;
    max-height: 100%;
}

ul,
ol,
li {
    list-style: none;
}
ul,
ol {
    padding-left: 0;
    margin-bottom: 15px;
}
li {
    padding-left: 30px;
    position: relative;
    margin-bottom: 10px;
}
.content li::before {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    background-color: $pink-color;
    transform: rotate(45deg);
    position: absolute;
    left: 7px;
    top: 5px;
}
a {
    text-decoration: none;
}
@media(max-width:1160px){
    .container {
        width: 100%;
    }
}