.about-us {
    background-color: #F8F8F8;

    &__wrapper {
        padding: 65px 60px;
        display: flex;
        column-gap: 90px;
    }

    &__title {
        @include font-raleway;
        font-size: 48px;
        font-weight: 700;
        line-height: 120%;
        text-transform: uppercase;
        letter-spacing: -0.05em;
        color: #000;
        text-shadow: $text-shadow;
        margin: 0;
        margin-bottom: 45px;

        span {
            color: #d18abd;
        }
    }

    &__info {
        max-width: 650px;
    }

    &__text {
        @include main-page-text;
        @include font-raleway;
        color: #000;
    }
}