.main-banner {
    background-color: #F8F8F8;
    position: relative;

    &__wrapper {
        display: flex;
        padding: 120px 60px 80px 60px;
        justify-content: space-between;
        min-height: 730px;
    }

    &__title {
        @include main-page-title;
        @include font-raleway;
        color: #000;
        text-shadow: 1px 4px 4px #00000040;
        margin-bottom: 75px;
    }
    &__subtitle {
        @include font-raleway;
        font-size: 40px;
        font-weight: 300;
        line-height: 120%;
        color: #000;
        letter-spacing: -0.04em;
        text-shadow: 1px 4px 4px #00000029;
    }
    &__info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__img {
        width: 325px;
        height: 325px;
        background-repeat: no-repeat;
        background-size: contain;
        align-self: center;
    }
    &__text {
        max-width: 600px;
    }
    &__btn {
        padding: 25px 55px;
        background-color: #d18abd;
        border-radius: 7px;
        color: #fff;
        max-width: 259px;
        font-size: 20px;
        font-weight: 800;
        line-height: 120%;
        @include font-raleway;
        box-shadow: $box-shadow;
        transition: $transition;
        border: 2px solid transparent;

        &:hover {
            background-color: #fff;
            color: #d18abd;
            border-color: #d18abd;
        }
    }
    &__silder-list {
        overflow: hidden;
    }
    &__slide-control {
        position: absolute;
        left: 50%;
        bottom: 25px;
        display: flex;
        column-gap: 15px;

        .swiper-pagination-bullet {
            cursor: pointer;
            width: 20px;
            height: 20px;
            border: 2px solid #000;
            background-color: #F8F8F8;
            opacity: 1;
            transition: $transition;

            &.swiper-pagination-bullet-active {
                background-color: #000;

                &:hover {
                    border-color: #000;
                }
            }

            &:hover {
                border-color: #d18abd;
            }
        }
    }
}