.faq {
    background-color: #fff;
    
    .container {
        padding: 65px;
        padding-bottom: 165px;
    }

    &__title {
        @include font-raleway;
        font-size: 48px;
        font-weight: 700;
        line-height: 120%;
        letter-spacing: -0.05em;
        color: #000;
        text-shadow: $text-shadow-dark;
        margin: 0;
        margin-bottom: 45px;
    }
    &__body {
        display: none;
    }
    &__item {
        border-bottom: 1px solid #ababab;
        transition: $transition;
        cursor: pointer;

        &.active {
            .faq__accr-control{
                transform: rotate(-45deg);
            }
            .faq__accr-name {
                color: $pink-color;
            }
        }

        &:hover {
            .faq__accr-name {
                color: $pink-color;
            }
        }
    }
    &__head {
        padding: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__body {
        background-color: $grey-color;
        padding: 25px;
    }
    &__accr-name {
        @include font-raleway;
        font-size: 32px;
        font-weight: 400;
        line-height: 120%;
        color: #000;
        letter-spacing: -0.04em;
        text-shadow: $text-shadow;
        transition: $transition;
    }
    &__accr-text {
        @include font-raleway;
        @include main-page-text;
        letter-spacing: -0.04em;
        color: $black-color;
    }
    &__accr-control {
        width: 50px;
        height: 50px;
        background-repeat: no-repeat;
        background-size: contain;
        transition: $transition;
    }
}